<template>
     <div class="login_container" :style="loginBg"> <!-- -->
        <div class="login_box">
          <!-- 文字描述 -->
        <div class="login_text">用户登入</div>
        <!-- 登陆表单 -->
        <el-form ref="loginFormRef" class="login_form" :model="loginForm" :rules="loginFormRules">
          <el-form-item prop="username">
            <input style="outline:none;" class="loginInput" v-model="loginForm.username" placeholder="账号">
          </el-form-item>
           <el-form-item prop="password">
            <input style="outline:none;" class="loginInput" v-model="loginForm.pwd" placeholder="密码">
          </el-form-item>
          <el-form-item class="btns">
             <el-button @click="login">登陆</el-button>
          </el-form-item>
        </el-form>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 背景
      loginBg: {
        backgroundImage: 'url(' + require('../assets/img/bgo.png') + ')',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover'
      },
      // 登陆表单的数据绑定对象
      loginForm: {
        username: '123',
        pwd: '123'
      },
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          { required: true, message: '请输入登陆名称', trigger: 'blur' }
        ],
        // 验证密码是否合法
        password: [
          { required: true, message: '请输入登陆密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async login () {
      if (this.loginForm.username === '' || this.loginForm.password === '') return this.$message.error('账号或密码有误')
      const { data: res } = await this.$http.post('login', this.loginForm)
      console.log(res)
      if (res.status !== 200) return this.$message.error('登陆失败')
      this.$message.success('登陆成功')
      // 1. 将登陆成功之后的token，保存到客户端的sessionStorage中
      //   1.1 项目中除了登陆之外的其他api接口，必须在登陆之后才能范文
      //   1.2 token只应在当前网站打开期间生效，所以将 token 保存在sessionStorage中
      console.log(res)
      window.sessionStorage.setItem('token', res.userStatus.token)
      // 2. 通过编程式导航跳转到后台主页，路由地址是/home
      this.$router.push('/home')
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
    height: 100%;
    // background: url(../assets/img/bgo.png) no-repeat;
}
.login_box{
    width: 380px;
    height: 300px;
    background-color: #fff;
    border-radius: 25px;
    position:absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 100px rgb(46, 25, 25);
  .login_text{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    color: rgb(173,58,31);
    font-size: 27px;
    letter-spacing: 0.2em;
  }
}

.btns{
    display: flex;
    justify-content: center;
  button{
    width: 200px;
    height: 50px;
    border-radius: 10px;
    font-size: 20px;
    background-color: rgb(173,58,31);
    letter-spacing: 0.2em;
    color: #fff;
  }
}

.login_form{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  .el-form-item{
    display: flex;
    justify-content: center;
  }
  .loginInput{
    width: 300px;
    height: 34px;
    border: none !important;
    font-size: 20px;
    border-bottom: 2px solid  rgb(173,58,31) !important;
  }
}
</style>
