<template>
    <div>
        <!--顶部区域  -->
        <div class="topRed"></div>
        <!-- 幕布主体区域 -->
        <div class="Curtain">
            <!-- 上部 -->
            <div class="CurTop">
                <img src="../assets/img/school.png" alt="">
                <div>高校教务教研教改项目查询系统</div>
            </div>
            <!-- 下部 -->
            <div class="CurBot">
              <!--<el-button size="small" icon="el-icon-menu" >显示</el-button>-->
              <el-popover placement="bottom" width="200" trigger="click">
                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                <el-checkbox-group v-model="checkedoptions" @change="handleCheckedDatasChange">
                  <el-checkbox v-for="item in colSelect" :label="item" :key="item" ></el-checkbox>
                </el-checkbox-group>
                <el-button class="left-button" slot="reference">显示</el-button>
              </el-popover>
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
                <el-input
                  prefix-icon="el-icon-search"
                  v-model="state"
                  placeholder="请输入内容" :trigger-on-focus="false" @keyup.enter.native="search(state)"></el-input>
                <el-button class="right-button" @click="search(state)">搜索</el-button>
            </div>
        </div>
        <!-- 按钮区 -->
        <div class="btns">
            <el-button size="small" icon="el-icon-download">模板下载</el-button>
            <el-button size="small" icon="el-icon-download" @click="imporRuItem">导入</el-button>
            <el-button size="small" icon="el-icon-share" @click="getExcel">导出</el-button>
            <el-button size="small" icon="el-icon-share" @click="addDialogVisible = true">单个添加</el-button>
            <el-button size="small" icon="el-icon-edit">修改</el-button>
            <el-button size="small" icon="el-icon-folder-checked" >保存</el-button>
            <el-button size="small" icon="el-icon-delete" >删除</el-button>
        </div>
        <!-- 主表区域 -->
        <div class="Mtable">
            <el-table :data="list" style="width: 100%" height="63vh" border stripe @row-dblclick="showEditDialog">
                <el-table-column type="index" fixed label="序号"> </el-table-column>
                <el-table-column v-if="colData[0].istrue" prop="id" label="项目编号" width="120px"> </el-table-column>
                <el-table-column v-if="colData[1].istrue" prop="title" label="项目名称" width="120px"> </el-table-column>
                <el-table-column v-if="colData[2].istrue" prop="host" label="主持人" width="70px"> </el-table-column>
                <el-table-column v-if="colData[3].istrue" prop="department" label="主持人所在部门" width="150px"> </el-table-column>
                <el-table-column v-if="colData[4].istrue" prop="member" label="项目组成员-排名-工资号" width="180px">
                    <template slot-scope="scope">
                        <el-col v-for="(item) in scope.row.member" :key="item.id">
                            <el-row>{{item.info}}</el-row>
                        </el-col>
                    </template>
                </el-table-column>
                <el-table-column v-if="colData[5].istrue" prop="start_time" label="申报时间" width="120px"> </el-table-column>
                <el-table-column v-if="colData[6].istrue" prop="end_time" label="结算时间" width="120px"> </el-table-column>
                <el-table-column v-if="colData[7].istrue" prop="project_status" label="项目状态" width="120px"> </el-table-column>
                <el-table-column v-if="colData[8].istrue" prop="funds" label="资助经费" width="120px"> </el-table-column>
                <el-table-column v-if="colData[9].istrue" prop="level" label="项目级别" width="100px"> </el-table-column>
                <el-table-column v-if="colData[10].istrue" prop="host_school" label="主持人所在学校" width="120px"> </el-table-column>
                <el-table-column v-if="colData[11].istrue" prop="coop_school" label="联合申请学校" width="120px"> </el-table-column>
                <el-table-column v-if="colData[12].istrue" prop="guide_id" label="指南题号" width="100px"> </el-table-column>
                <el-table-column v-if="colData[13].istrue" prop="recommend" label="推荐单位" width="120px"> </el-table-column>
                <el-table-column v-if="colData[14].istrue" prop="code" label="代码" width="120px"> </el-table-column>
            </el-table>
        </div>
        <!-- 修改用户的对话框 -->
        <el-dialog
          title="提示"
          :visible.sync="editDialogVisible"
          width="50%" @close="editDialogClosed">
          <el-form ref="editFormRef" :model="editForm"  label-width="70px">
            <el-form-item label="项目编号">
              <el-input v-model="editForm.id" disabled></el-input>
            </el-form-item>
            <el-form-item label="主持人所在部门" prop="department">
              <el-input v-model="editForm.department"></el-input>
            </el-form-item>
            <el-form-item label="结算时间" prop="end_time">
              <el-input v-model="editForm.end_time"></el-input>
            </el-form-item>
            <el-form-item label="项目状态" prop="project_status">
              <el-input v-model="editForm.project_status"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button @click="removeUser">删 除</el-button>
            <el-button type="primary" @click="editUserInfo">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 添加用户的对话框 -->
        <el-dialog
          title="添加用户"
          :visible.sync="addDialogVisible"
          width="50%" @close="addFialogClosed">
          <!-- 内容主体区域 -->
            <el-form ref="addFormRef" :model="addForm" label-width="150px" status-icon>
              <el-form-item label="项目编号" prop="id">
                <el-input v-model="addForm.id"></el-input>
              </el-form-item>
              <el-form-item label="项目名称" prop="title">
                <el-input v-model="addForm.title"></el-input>
              </el-form-item>
              <el-form-item label="主持人" prop="host">
                <el-input v-model="addForm.host"></el-input>
              </el-form-item>
              <el-form-item label="主持人所在部门" prop="department">
                <el-input v-model="addForm.department"></el-input>
              </el-form-item>
              <el-form-item :label="'项目组成员-排名-工资号' + index"
              v-for="(member, index) in addForm.member" :key="member.id"
                :prop="'member.' + index + '.info'"
                :rules="{
                  required: true, message: '不能为空', trigger: 'blur'
                }" class="member">
                <el-input v-model="member.info"></el-input><el-button @click.prevent="removeDomain(member)">删除</el-button>
              </el-form-item>
              <el-form-item label="申报时间" prop="start_time">
                <el-input v-model="addForm.start_time"></el-input>
              </el-form-item>
              <el-form-item label="结算时间" prop="end_time">
                <el-input v-model="addForm.end_time"></el-input>
              </el-form-item>
              <el-form-item label="项目状态" prop="project_status">
                <el-input v-model="addForm.project_status"></el-input>
              </el-form-item>
              <el-form-item label="资助经费" prop="funds">
                <el-input v-model="addForm.funds"></el-input>
              </el-form-item>
              <el-form-item label="项目级别" prop="level">
                <el-input v-model="addForm.level"></el-input>
              </el-form-item>
              <el-form-item label="主持人所在学校" prop="host_school">
                <el-input v-model="addForm.host_school"></el-input>
              </el-form-item>
              <el-form-item label="联合申请学校" prop="coop_school">
                <el-input v-model="addForm.coop_school"></el-input>
              </el-form-item>
              <el-form-item label="指南题号" prop="guide_id">
                <el-input v-model="addForm.guide_id"></el-input>
              </el-form-item>
              <el-form-item label="推荐单位" prop="recommend">
                <el-input v-model="addForm.recommend"></el-input>
              </el-form-item>
              <el-form-item label="代码" prop="code">
                <el-input v-model="addForm.code"></el-input>
              </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button @click="addDomain">新增项目组成员</el-button>
            <el-button type="primary" @click="addUser">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 上传excel对话框 -->
        <el-dialog title="导入excel" v-if="dialogVisible" :visible.sync="dialogVisible" width="40%">
      <el-upload
        class="ml-10"
        :limit="limitNum"
        :auto-upload="false"
        accept=".xls"
        action="http://Cambria.f3322.net:23333/ERPQS/uploadXls"
        :before-upload="beforeUploadFile"
        :on-change="fileChange"
        :on-exceed="exceedFile"
        :on-success="handleSuccess"
        :on-error="handleError"
        :file-list="fileList"
      >
        <el-button slot="trigger" size="small" type="primary">导入资产</el-button>
        <!-- <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div> -->
        <div class="el-upload__tip" slot="tip">只能上传xls文件，且不超过10M</div>
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="uploadFile">确 定</el-button>
      </span>
    </el-dialog>
    </div>
</template>

<script>
const ExportJsonExcel = require('js-export-excel')
const colOptions = ['项目编号', '项目名称', '主持人', '主持人所在部门', '项目组成员-排名-工资号', '申报时间', '结算时间', '项目状态', '资助经费', '项目级别', '主持人所在学校', '联合申请学校', '指南题号', '推荐单位', '代码']
export default {
  data () {
    return {
      diseaseList: [], // 这个值是需要匹配的值
      state: '', // 这个值是用户输入的值
      limitNum: 1, // 上传excell时，同时允许上传的最大数
      fileList: [], // excel文件列表
      dialogVisible: false,
      checkAll: true,
      isIndeterminate: false,
      colSelect: colOptions,
      // 默认全选
      checkedoptions: ['项目编号', '项目名称', '主持人', '主持人所在部门', '项目组成员-排名-工资号', '申报时间', '结算时间', '项目状态', '资助经费', '项目级别', '主持人所在学校', '联合申请学校', '指南题号', '推荐单位', '代码'],
      // 所有表头标题
      colData: [
        { title: '项目编号', istrue: true },
        { title: '项目名称', istrue: true },
        { title: '主持人', istrue: true },
        { title: '主持人所在部门', istrue: true },
        { title: '项目组成员-排名-工资号', istrue: true },
        { title: '申报时间', istrue: true },
        { title: '结算时间', istrue: true },
        { title: '项目状态', istrue: true },
        { title: '资助经费', istrue: true },
        { title: '项目级别', istrue: true },
        { title: '主持人所在学校', istrue: true },
        { title: '联合申请学校', istrue: true },
        { title: '指南题号', istrue: true },
        { title: '推荐单位', istrue: true },
        { title: '代码', istrue: true }
      ],
      // 主体表单数据
      // list: [{
      //   id: 7,
      //   title: '测试第一个数据',
      //   host: '凯老板',
      //   department: '琴理工作室',
      //   member: ['m1-r1-s1', 'm2-r2-s2', 'm3-r3-s3'],
      //   start_time: '2020.20.20',
      //   end_time: '2020.20.22',
      //   project_status: '已完工',
      //   funds: 'AAA',
      //   level: 'bbb',
      //   host_school: '沈阳理工大学',
      //   coop_school: '东北大学',
      //   guide_id: '1-1',
      //   recommend: '奥利给',
      //   code: '5050'
      // }],
      // {
      //   id: 7,
      //   title: '测试第一个数据',
      //   host: '凯老板',
      //   department: '琴理工作室',
      //   member: [{
      //     id: 1,
      //     info: '郭洪良1-1-1'
      //   },
      //   {
      //     id: 2,
      //     info: '郭洪良2-2-2'
      //   }],
      //   start_time: '2020.20.20',
      //   end_time: '2020.20.22',
      //   project_status: '已完工',
      //   funds: 'AAA',
      //   level: 'bbb',
      //   host_school: '沈阳理工大学',
      //   coop_school: '东北大学',
      //   guide_id: '1-1',
      //   recommend: '奥利给',
      //   code: '5050'
      // },
      // {
      //   id: 7,
      //   title: '测试第一个数据',
      //   host: '凯老板',
      //   department: '琴理工作室',
      //   member: [{
      //     id: 1,
      //     info: '郭洪良1-1-1'
      //   },
      //   {
      //     id: 2,
      //     info: '郭洪良2-2-2'
      //   }],
      //   start_time: '2020.20.20',
      //   end_time: '2020.20.22',
      //   project_status: '已完工',
      //   funds: 'AAA',
      //   level: 'bbb',
      //   host_school: '沈阳理工大学',
      //   coop_school: '东北大学',
      //   guide_id: '1-1',
      //   recommend: '奥利给',
      //   code: '5050'
      // },
      // {
      //   id: 7,
      //   title: '测试第一个数据',
      //   host: '凯老板',
      //   department: '琴理工作室',
      //   member: [{
      //     id: 1,
      //     info: '郭洪良1-1-1'
      //   },
      //   {
      //     id: 2,
      //     info: '郭洪良2-2-2'
      //   }],
      //   start_time: '2020.20.20',
      //   end_time: '2020.20.22',
      //   project_status: '已完工',
      //   funds: 'AAA',
      //   level: 'bbb',
      //   host_school: '沈阳理工大学',
      //   coop_school: '东北大学',
      //   guide_id: '1-1',
      //   recommend: '奥利给',
      //   code: '5050'
      // }
      // ],
      data: [],
      list: [],
      // 控制修改对话框的显示与隐藏
      editDialogVisible: false,
      // 查询到的信息对象
      editForm: {},
      userlist: [],
      // 控制添加用户对话框的显示以及隐藏
      addDialogVisible: false,
      // 添加用户的表单数据
      addForm: {
        id: '',
        title: '',
        host: '',
        department: '',
        member: [],
        start_time: '',
        end_time: '',
        project_status: '',
        funds: '',
        level: '',
        host_school: '',
        coop_school: '',
        guide_id: '',
        recommend: '',
        code: ''
      },
      // select下拉框内容
      options: [{
        value: '1',
        label: '按照主持人所在部门查询'
      }, {
        value: '2',
        label: '按照工资号查询'
      }, {
        value: '3',
        label: '按照姓名查询'
      }],
      value: ''
    }
  },
  watch: {
    checkedoptions (valArr) {
      var arr = this.colSelect.filter(i => valArr.indexOf(i) < 0) // 未选中
      this.colData.filter(i => {
        if (arr.indexOf(i.title) !== -1) {
          i.istrue = false
          this.$nextTick(() => {
            this.$refs.tableDataRef.doLayout()
          })
        } else {
          i.istrue = true
          this.$nextTick(() => {
            this.$refs.tableDataRef.doLayout()
          })
        }
      })
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 全选框
    handleCheckAllChange (val) {
      this.checkedoptions = val ? colOptions : []
      this.isIndeterminate = false
    },
    // 控制全选框状态
    handleCheckedDatasChange (value) {
      const checkedCount = value.length
      this.checkAll = checkedCount === this.colSelect.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.colSelect.length
    },
    async getUserList () {
      const { data: res } = await this.$http.post('query', { query_by: 0 })
      console.log(res)
      if (res.status !== 200) {
        return this.$message.error('获取用户列表失败')
      }
      this.list = res.list
      console.log(res)
    },
    // 删除对应的用户信息
    async removeUser () {
      // 弹框询问用户是否删除数据
      const confirmResult = await this.$confirm('此操作将永久删除该项目信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 如果用户确认删除 返回值为字符串 confirm
      // 如果用户取消了删除，则返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.post('delete', { del: this.editForm.id })
      if (res.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      this.editDialogVisible = false
      this.getUserList()
    },
    // 监听添加用户对话框的关闭事件
    addFialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 点击按钮，添加新用户
    addUser () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 可以发起添加用户的网络请求
        const { data: res } = await this.$http.post('insert', this.addForm)
        if (res.status !== 200) {
          this.$message.error('添加用户失败')
        }
        this.$message.success('添加用户成功')
        // 重新把对话框隐藏
        this.addDialogVisible = false
        // 重新获取用户列表数据
        this.getUserList()
      })
      console.log(this.addForm)
      console.log(this.addForm.member)
    },
    // 监听修改用户对话框的关闭事件
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 展示编辑用户的对话框
    async showEditDialog (row) {
      console.log(row.id)
      const { data: res } = await this.$http.post('query', { key: row.id, query_by: 4 })
      if (res.status !== 200) {
        return this.$message.error('查询用户信息失败')
      }
      this.editForm = res.list[0]
      console.log(this.editForm)
      this.editDialogVisible = true
    },
    // 修改用户信息并且提交
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 可以发起修改用户信息提交的网络请求
        const { data: res } = await this.$http.post('update', this.editForm)
        if (res.status !== 200) {
          return this.$message.error('更新信息失败')
        }
        this.editDialogVisible = false
        this.getUserList()
        this.$message.success('更新信息成功')
      })
    },
    // 删除input
    removeDomain (item) {
      var index = this.addForm.member.indexOf(item)
      if (index !== -1) {
        this.addForm.member.splice(index, 1)
      }
    },
    // 添加input 顺便把值push到member数组中
    addDomain () {
      this.addForm.member.push({
        info: ''
      })
    },
    // 下载excel
    getExcel () {
      this.data = JSON.stringify(this.list)
      this.data = JSON.parse(this.data)
      for (var j = 0; j < this.list.length; j++) {
        this.data[j].member = ''
        for (var i = 0; i < this.list[j].member.length; i++) {
          this.data[j].member += '['
          this.data[j].member += this.list[j].member[i].info
          this.data[j].member += '],'
        }
      }
      console.log(this.data)
      var option = {}
      option.fileName = 'excel'
      option.datas = [
        {
          // 后端返回的数据表格json数据
          sheetData: this.data,
          sheetHeader: ['项目编号', '项目名称', '主持人', '主持人所在部门', '项目组成员-排名-工资号', '申报时间', '结算时间', '项目状态', '资助经费', '项目级别', '主持人所在学校', '联合申请学校', '指南题号', '推荐单位', '代码'] // 前端指定了表头
        }
      ]
      var toExcel = new ExportJsonExcel(option)
      toExcel.saveExcel()
    },
    UploadUrl () {
      // 因为action参数是必填项，我们使用二次确认进行文件上传时，直接填上传文件的url会因为没有参数导致api报404，所以这里将action设置为一个返回为空的方法就行，避免抛错
      return ''
    },
    // 文件超出个数限制时的钩子
    exceedFile (files, fileList) {
      this.$message.warning(
        `只能选择 ${this.limitNum} 个文件，当前共选择了 ${files.length + fileList.length} 个`
      )
    },
    // 文件状态改变时的钩子
    fileChange (file, fileList) {
      console.log(file.raw)
      this.fileList.push(file.raw)
      console.log(this.fileList)
    },
    // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
    beforeUploadFile (file) {
      console.log('before upload')
      console.log(file)
      const extension = file.name.substring(file.name.lastIndexOf('.') + 1)
      const size = file.size / 1024 / 1024
      if (extension !== 'xls') {
        this.$message.warning('只能上传后缀是.xls的文件')
      }
      if (size > 10) {
        this.$message.warning('文件大小不得超过10M')
      }
    },
    // 文件上传成功时的钩子
    handleSuccess (res, file, fileList) {
      this.$message.success('文件上传成功')
    },
    // 文件上传失败时的钩子
    handleError (err, file, fileList) {
      this.$message.error('文件上传失败')
      console.log(err)
    },
    // 导入表格
    imporRuItem () {
      console.log(this.dialogVisible)
      this.dialogVisible = true
      this.$nextTick(() => {
        console.log(this.$refs)
        console.log(this.$refs.dialogCom)
        this.$refs.dialogCom.dialogVisible = true
      })
    },
    // 发送请求
    uploadFile (form) {
      if (this.fileList.length === 0) {
        this.$message.warning('请上传文件')
      } else {
        const form = new FormData()
        form.append('file', this.fileList[0])
        this.$emit('uploadFile', form)
        this.fileList = []
        // 发送请求
        this.uploadXlsx('uploadXls', form).then(res => {
          this.dialogVisible = false
          this.fetchData() // 重新获取自己表格列表的数据
        })
      }
    },
    // 搜索
    async search (state) {
      if (!state) {
        return this.$message.warning('请输入您要搜索的内容')
      }
      console.log(this.state)
      console.log(this.value)
      const { data: res } = await this.$http.post('query', { query_by: this.value, key: this.state })
      if (res.status !== 200) {
        return this.$message.error('未搜索到您想要的内容')
      }
      this.list = res.list
    }
  }
}
</script>

