import Vue from 'vue'
import qs from 'qs'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
// 全局样式表导入
import './assets/css/global.css'
// 导入封装好的表格请求文件
import { uploadXlsx } from './assets/js/importExcel.js'
import axios from 'axios'
// 配置请求根路径
// axios.defaults.baseURL = 'http://82.156.27.146:8080/EduReformProjectQuerySys/'
axios.defaults.baseURL = 'http://Cambria.f3322.net:23333/ERPQS/'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
axios.interceptors.request.use(config => {
  console.log(config)
  config.headers.token = window.sessionStorage.getItem('token')
  // config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
  // config.headers.'Content-Type' = 'application/x-www-form-urlencoded; charset=UTF-8'
  // 最后必须return config
  return config
})
axios.interceptors.request.use(config => {
  if (config.method === 'post') {
    config.data = qs.stringify(config.data)
  }
  return config
}, error => {
  Promise.reject(error)
})
Vue.prototype.$http = axios
Vue.prototype.uploadXlsx = uploadXlsx
Vue.config.productionTip = false
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
